import React, { useState } from "react"
import styles from "./member.module.css"
import { ICard } from "../../../../api/dto/Card"
import { Select, Input } from 'antd';
import { useHistory } from "react-router-dom";
import classnames from "classnames"
import globalStyle from "../../../../core/theme/commonStyle.module.css"
const { Option } = Select;
const { TextArea } = Input;
interface IProps {
    selected: ICard;
}

export const Member: React.FC<IProps> = ({ selected }) => {
    const [value, setValue] = useState('');
    const history = useHistory();
    const handleNext = () => {
        history.push(`/rumb/payment${selected.id}/pay`)
    }
    return (
        <div>
            <div className={styles.memberTitle}>
                   ?
            </div>
            <div>
                <div className={styles.member}></div>                
                <Select
                    defaultValue={"1 "}
                    showSearch
                    className={styles.selectContainer}
                    optionFilterProp="children"
                >
                    {Array(selected.seats).fill(0).map((e,i)=>i+1).map(index => (
                        <Option key={index} value={index}>
                            {index === 1
                            ? `${index} `
                            : index>1 && index<5? `${index} `
                            : index > 4? `${index} `
                            : ""
                            }
                        </Option>
                    ))}
                </Select>
            </div>
            <div className={styles.member}>
                   
            </div>
            <div className={styles.textArea}>
                <TextArea
                    value={value}
                    placeholder='!   .'     
                    onChange={(text)=>{setValue(text.target.value)}}
                    autoSize={{ minRows: 4 }}
                />
            </div>
            <div onClick={handleNext} className={classnames(globalStyle.buttonOrder, styles.btnContainer)}></div>       
        </div>
    )
}